@import '../scss/variables.scss', '../scss/_mixins.scss';

.navbar {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    background-color: $cardNavBgColor;
    a {
        color: white;
        font-size: 2rem;
        padding: 1rem;
        text-decoration: none;
        font-weight: bold;
    }

    a:hover {
        background-color: $backgroundColor;
        color: $cardNavBgColor;
    }
}

@include media-xsm{
    .navbar{
        flex-direction: column;
        text-align: center;
    }
}

@include media-sm{
    .navbar{
        flex-direction: row;
    }
}